#header {
    position: static;
    top: 0;
    left: 0;
    width: 100%;
    height: 60px;
    background-color: #bbbbbb;
    padding: 0 10px 0 10px;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    -webkit-box-shadow: 0px 6px 16px 1px rgba(0, 0, 0, 0.2);
    -moz-box-shadow: 0px 6px 16px 1px rgba(0, 0, 0, 0.2);
    box-shadow: 0px 6px 16px 1px rgba(0, 0, 0, 0.2);
    .btn-toogle {
        position: relative;
        width: 40px;
        height: 40px;
        background-color: #ffd04a;
        border: 2px solid tomato;
        color: #ffd04a;
        cursor: pointer;
        border-radius: 10px;
        transform: rotate(0deg);
        -webkit-transition: -webkit-transform .8s ease-in-out;
        transition: transform 0.5s;
        background-image: url(../image/menu.png);
        background-size: cover;
        box-sizing: border-box;
        &:hover {
            background-color: #ffd04a;
            border: 2px solid #444141;
            display: block; } }
    .main-menu {
        position: absolute;
        top: 60px;
        left: 0px;
        margin: 0;
        padding-left: 0px;
        padding-right: 2px;
        background-color: #e7e7e7c2;
        z-index: 100;
        cursor: pointer;
        font-weight: normal;
        -webkit-transition: -webkit-transform .8s ease-in-out;
        transition: transform .8s ease-in-out;
        font-size: 0.9rem;
        font-family: "MontserratMedium";
        font-weight: normal;
        color: #3c3c3c;
        font-weight: bold;
        .menu-item {
            padding-left: 20px;
            padding-right: 2px;
            cursor: pointer;
            font-weight: normal;
            //display: none
            list-style-type: none;
            &:hover {
                font-weight: bold; }
            .sub-menu-item {
                cursor: pointer;
                font-weight: normal;
                transition: all 0.5s ease-in;
                //display: none
                &:hover {
                    font-weight: bold; } } } } }

.hide::before {
    content: '\2795';
    margin-right: 5px;
    font-size: 0.7rem; }

.show::before {
    content: '\2796';
    margin-right: 5px;
    font-size: 0.7rem; }

